#cart {
  padding-top: 8rem;
}

.cartArea {
  display: flex;
  gap: 1rem;
  justify-content: space-evenly;
}

.cartHeading {
  margin: 1rem;
}

.leftCartArea {
  width: 62%;
}

.cartArea .leftCartArea li {
  list-style: none;
}

/* ################################################################## CART PRODUCT LIST ITEM ####################################################333 */

.cartProduct {
  background-color: lavender;
  padding: 1rem;
  display: flex;
  justify-content: space-evenly;
  gap: 1rem;
}

.cartProduct img {
  display: inline-block;
  background-color: lightslategray;
  height: 16rem;
  width: 125px;
}

.cartProductDetails {
  width: 70%;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}

.RemoveCartItemBtn {
  cursor: pointer;
  font-weight : bold;
  height: fit-content;
  padding: 0.1rem 0.6rem;
}
.RemoveCartItemBtn:hover {
  color: rgb(204, 2, 2);
  background-color: #bcbcbc;
  border-radius: 1rem;
}

.selectQty {
  display: block;
  margin-top: 1rem;
}

/* __________________________________________________________________RIGHT CART SECTION________________________________________________________________- */

.rightSection {
  width: 30%;
  padding: 2rem 1.5rem;
}

.rightSection .CartDetails p,
.rightSection .CartDetails h2 {
  display: inline;
}

.CartDetailsLine {
  display: flex;
  justify-content: space-between;
  margin: 1rem 0;
  gap: 0.5rem;
}

.CDLValue {
  text-align: right;
}

.btnContainerCart {
  text-align: center;
}

.placeOrderBtnCart {
  width: 100%;
  margin-top: 2rem;
  padding: 0.8rem;
}

.detailsBtnContainer {
  text-align: end;
  padding-right: 10%;
  padding-bottom: 1rem;
  background-color: lavender;
  margin-bottom: 0.5rem;
}

.detailsBtn {
  color: white;
  border: 1px solid black;
  background-color: #898989;
  text-align: center;
  margin-top: 1rem;
  padding: 0.2rem 2rem;
  border-radius: 1rem;
  transition: all 0.4s ease-in-out;
}
.detailsBtn:hover {
  background-color: white;
  color: black;
  border-color: black;
}

.clearCart,
.gotoProductsLink {
  color: #676767;
  font-weight: bold;
  cursor: pointer;
  transition: color 0.2s ease-in-out;
}
.clearCart:hover,
.gotoProductsLink:hover {
  color: mediumspringgreen;
}

/* _______________________________________________________________________________________________________________________________________________ */

@media (max-width: 575px) {
  .cartArea {
    display: block;
    width: 100%;
  }

  .leftCartArea,
  .rightSection {
    width: 100%;
  }
}

@media (max-width: 375px) {
  .cartProduct {
    display: block;
  }

  .cartProduct img {
    display: block;
    background-color: lightblue;
    height: 100px;
    width: 90px;
  }

  .cartProductDetails {
    width: 100%;
  }

  .detailsBtnContainer {
    text-align: start;
    padding-left: 1rem;
    margin-bottom: 2rem;
  }

  .CartDetails {
    border: 1px solid black;
    padding: 2rem;
  }

  .cartProduct {
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.5);
  }
}
