#Products {
  padding: 9rem 1rem;
  padding-left: 1.5rem;
  padding-bottom: 0rem;
  
}

#sectionContainer {
  display: flex;
  justify-content: flex-start;
  gap: 1.5rem;
}

#productsFilters {
  /* background: rgba(255, 228, 196, 0.45); */
  background : url(../../images/contactBg.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  border-radius: 1rem;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.5);
  border: 1px solid black;
  width: 25%;
  min-width: 20rem;
  height: fit-content;
  text-align: center;
  margin-bottom: 2rem;
}

#OtherFilers select {
  text-align: center;
  width: 80%;
}

/* **************************************************   SEARCH AREA    ************************************************ */

#searchArea {
  padding: 2rem 0;
}

.searchFilter {
  border-radius: 1rem;
  border: none;
  padding: 0.4rem 1rem;
  width: 80%;
}

.searchFilterBtn {
  margin: 2rem auto;
  font-weight: bold;
  background-color: white;
  padding: 0.4rem 0.8rem;
  width: 80%;
  border: 2px solid #898989;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.5);
}
.searchFilterBtn:hover {
  transform: scale(0.95);
  color: mediumspringgreen;
  border: 1px solid mediumspringgreen;
}
.searchFilterBtn:active {
  color: midnightblue;
}

.textAlignLeft {
  text-align: left;
  padding: 1rem;
  padding-left: 1.5rem;
  font-weight: bold;
}

.filterBtn {
  margin-top: 4rem;
}

.clearFilters {
  color: black;
  margin: 4rem auto;
  margin-top: 0rem;
  display: block;
  font-weight: bold;
  width: fit-content;
  padding: 0.4rem 0.8rem;
  transition: all 0.4s ease-in-out;
}
.clearFilters:hover {
  color: mediumspringgreen;
}
.clearFilters:active {
  color: black;
}

/* __________________________________________________________   PRODUCT LISTING ______________________________________________________________ */

#productsList {
  padding: 1rem auto;
}

.productsList {
  display: flex;
  width: fit-content;
  flex-wrap: wrap;
  justify-content: space-around;
  min-height: 80vh;
}

/* __________________________________________________________   PAGE NUMBERS (PAGINATION) ______________________________________________________________ */
.pages {
  padding: 0 auto;
  padding-bottom: 1.5rem;
}

.pagesList {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  width: 100%;
  height: 50px;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

.pageNoBtn {
  text-align: center;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.5);
  transition: all 0.4s ease-in;
  cursor: pointer;
}
.pageNoBtn:hover {
  transform: scale(1.15);
  color: mediumspringgreen;
  background-color: rgba(255, 255, 255, 0.5);
}

/* ________________________________________________________________________________________MEDIA QUERIES_______________________________________________________________________________________________ */

@media (max-width: 525px) {
  #sectionContainer {
    display: block;
  }

  #productsFilters {
    width: 100%;
  }

  #searchArea {
    padding-bottom: 0;
  }

  .pagesList {
    flex-wrap: wrap;
    gap: 3rem;
    justify-content: space-evenly;
  }
}