.hide{
    display: none;
}

#mainMenu{
    position: fixed;
    width: 100%;
    z-index: 1199;
    color: white;
}

.default{
    background-color: rgba(0, 0, 0,0.4);
}

.dark{
    background-color: rgb(221, 240, 233);
    color: black;
    border-bottom: 2px solid mediumspringgreen;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
}

.darkLogo{
    color: mediumspringgreen;
}

.darkNavLink{
    color: #343434;
}

.lightNavLink{
    color: white;
}

.active{
    color: mediumspringgreen;
}

#mainMenu li {
    list-style: none;
}

#simpleMainMenu{
    height: 60px;;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menuOption{
    display: flex;
    width: 45%;
    gap: 5%;
}

#mainMenu .listItem{
    flex:1 1 20%;
    align-items: center;
    text-align: center;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.4s ease-in;
}
#mainMenu .listItem:hover{
    color: mediumspringgreen;
    transform: scale(1.1);
    border-left: 1px solid mediumspringgreen;
    border-right: 1px solid mediumspringgreen;
}

#logo, #userOptions , #dropdownMenu{
    margin: 0 3rem;
    font-weight: bold;
}

/* ############################################ Category #################################################### */

#mainMenu .categories{
    font-weight: bold;
    cursor: pointer;
}

.categoriesOptions{
    position:absolute;
    padding: 1rem 2.5rem;
    width: 22rem;
    padding-top: 3rem;
    background: rgba(25,25,25,0.9);
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.6);
    cursor: auto;
}

.categoryOption{
    display: inline-block;
    margin-bottom: 0.5rem;
    cursor: pointer;
    text-align: start;
    transition: all 0.4s ease-in;
}
.categoryOption:hover{
    transform: scale(1.1);
    font-weight: normal;
    color: mediumspringgreen;
}

/* ################################################################# PARTIAL DROPDOWN ##################################################################*/

.logInSignUp{
    display: inline;
}

.hideLogInSignUp{
    display: none;
}

.partialDDIcon, .dropdownIcon{
    font-weight: bold;
}

.partialDropdown{
    position: absolute;
    right: 2%;
    top: 84%;
    text-align: center;
    border: 1px solid black;
    background: rgba(25,25,25,0.9);
    margin: 1rem 0;
    padding: 2rem 1rem;
    padding-top: 4rem;
    width: 32rem;
    min-width: 25rem;
    height: 45vh;
    min-height: 100%;
    display: inline;
}

.hidePartialDropDown{
    display: none;
}

/* _______________________________________________________ DROPDOWN ______________________________________ */

#dropdownMenu{
    display: none;
}

.DropdownListItem{ 
    display: inline-block;
    width:90%;
    padding: 0.4rem 2rem;
    text-align: center;
    margin :1rem auto;
    border-radius: 5rem;
    border: 2px solid rgba(25,25,25,0.1);
    transition: all 0.4s ease-in;
}

.DropdownListItem:hover{
     transform: scale(1.1);
     background-color: white;
     color:mediumspringgreen;
     border: 2px solid mediumspringgreen;
     cursor: pointer;
}

.mainDropdownOption{
    position: absolute;
    left: 0%;
    width:100%;
    min-height: 50vh;
    height: 90vh;
    margin: 1rem 0;
    padding: 2rem 1rem;
    text-align: center;
    background: rgba(5,5,5,0.9);
}

/* ***********************************************************************  MEDIA QUERIES ******************************************************************************* */

@media(max-width:825px){
    #mainMenu .listItem, #mainMenu .categories, .DropdownListItem{
        font-size: 2rem;
    }
 
    .logInSignUp{
        font-size: 1.25rem !important;
    }
}

@media(max-width:765px){
    .menuOption,#userOptions{
        display: none;
    }
 
    #dropdownMenu{
        display: block;
    }
}