#contact {
  padding: 4rem;
  padding-top: 12rem;
  background : url(../../images/contactBg.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.icon-link {
  display: block;
}

.icon-link-text {
  display: block;
  padding: 0.6rem 0;
  font-weight: bold;
  color: #454545;
}

.contact-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.contact-links a {
  text-align: center;
  align-content: center;
}

.contact-method {
  padding: 1rem;
  margin: 1rem;
  width: 10rem;
  height: 10rem;
  transform: translateY(+9%);
  transition: all 0.5s ease-in-out;
}

.contact-method:hover {
  transform: translateY(-5%);
}

iframe {
  box-shadow: 0px 0px 15px 0px rgba(100, 100, 100, 0.8);
  margin: 3rem 0;
}

@media (max-width : 450px){
  .contact-links {
    flex-wrap: wrap;
  }
  #contact {
    background-color: lightyellow;
  }
   
}