#LogIn{
    border: 1px soild green;
    padding-top: 3rem;
}

.logIn{
    background-color: rgba(0,255,154,0.8);
    padding: 5rem;
    width: 50%;
    margin: 5rem auto;
    margin-top: 11rem;
    border-radius: 5rem;
    outline: 1px solid rgba(0,0,0,0.5);
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.6);
}

.formHeading{
    margin-bottom: 5rem;
    text-align: center;
}

.LoginSignUpInput{
    display: block;
    padding: 1rem 2rem;
    border: none;
    color: mediumspringgreen;
    font-weight: bold;
    border-radius: 1rem;
    width: 100%;
}

.LoginSignUpInput:focus{
    border-bottom: 2px solid mediumspringgreen;
    outline: none;    
}

.LoginSignUpBtn{
    width: 100%;
    padding: 1rem 2rem;
    display: block;
    border-radius: 1rem;
    font-weight: bold;
    border: 1px solid #676767;
    cursor: pointer;
    transition: all 0.4s ease-in;
}
.LoginSignUpBtn:hover{
    color: mediumspringgreen;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.6);
    transform: scale(1.02);
}
.LoginSignUpBtn:active{
    color: rgb(15, 235, 150);
    transform: scale(0.96);
}

.AnotherFormLink{
    transition: all 0.4s ease-in-out;
    color: black;
    padding: 0.5rem;
}
.AnotherFormLink:hover{
    color: white;
}


@media(max-width:575px){
    .logIn{
        padding: 4rem 2rem;
        width: 90%;
        margin: 5rem auto;
        margin-top: 9rem;
        border-radius: 2.5rem;
       
    }
}