#footer li{
  list-style: none;
}

#footer{
  width: 100%;
  background-color: #212121dd;
  position: relative;
  bottom: 0%;
  z-index: 98;
  color: white;
  margin-top: 6rem;
  padding: 2rem 1.5rem;
  padding-right: 0.5rem;
  padding-bottom: 0;
}

.footerContent {
  display: flex;
  width: 100%;
  gap: 0.6rem;
  justify-content: space-evenly;
  margin: 2rem 0;
}

.footerSubContainer{
  flex : 1 1 20%;
  padding: 2rem 0;
  text-align: center;
}

.footerHeadline{
  padding: 0.5rem 0;
  margin-bottom: 1.5rem;
}

.footerText{
  padding: 0.2rem;
  cursor: pointer;
  margin: 0.6rem auto;
  display: block;
  width: fit-content;
  transition: all 0.2s ease-in-out;
}
.footerText:hover{
  color: mediumspringgreen;
  transform: scalex(1.02);
}

.followLinksFooter{
  display: flex;
  gap: 0.5rem;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.footerIcon{
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;
}
.footerIcon:hover{
  transform: rotate(360deg);
}

.bottomLine{
  padding: 2rem 0;
  text-align: center;
  background-color: #343434;
  color: whitesmoke;
}


.company{
  flex: 1 1 25%;
  text-align: left;
}
.TBC{
  display: block;
}
.categoryFooter{
  flex: 1 1 17.5%;
}
.followUs{
  flex: 1 1 25%;
}

.siteFooter{
  flex: 1 1 15%;
}

.terms&Conditions{
  flex: 1 1 12.5%;
}

/* _________________________________________________________________________________________________________ */
@media (max-width: 560px) {
  .footerContent {
    display: block;
  }
  .fContent {
    margin: 1rem 0;
    padding: 1.5rem 1rem;
  }
}