#ANPFormContainer{
    padding: 10rem 0rem;
    padding-bottom: 2rem;
}

.formAddNewPoduct{
    padding: 5rem 2rem;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.4);
    text-align: center;
    border: 2px solid #454545;
    width: 90%;
    margin: 1rem auto;
    background-color:honeydew;
    border-radius: 2.5rem;
}

.formAddNewPoduct input{
    background-color: honeydew;
}

.ANPHeading{
    text-align: center;
}

.formAddNewPoduct .input{
    display: inline-block;
    width: 90%;
    font-weight: bold;
    padding: 1rem 1rem;
    border: none;
    border-bottom : 1px solid black;
}
.formAddNewPoduct .input:focus{
    border: none;
    outline: none;
    color: mediumspringgreen;
    border-bottom: 2px solid mediumspringgreen;

}

.ANPFlexContainer{
    display: flex;
    width: 90%;
    margin: 0 auto;
    gap: 2rem;
}

.formAddNewPoduct select{
    width: 50%;
    min-width: fit-content;
    border: none;
    padding: 0.4rem 1rem;
}

.inputLabel{
    display: block;
    font-weight: bold;
    color: #666;
    width: fit-content;

}
.inputFile{
    display: inline !important;
    width: fit-content;
}



.ANPBtnContainer{
    text-align: center;
}

.ANPBtnContainer button{
    margin: 1rem auto;
    width: 60%;
}

/* __________________________________________________________________________________ */

@media(max-width:530px){
    .ANPFlexContainer{
        display: block;
    }
}

@media(max-width:306px){
    .formAddNewPoduct{
        padding: 4rem 1.8rem;
        border: none;
        width: 100%;
        margin: 0;
        border-radius: 0.5rem;
    }
}