#about{
    padding: 3rem;
    padding-top: 10rem;
}

.story{
    display: flex;
    margin-top: 8rem;
    min-height: 70vh;
}

.aboutImage{
    width: 40%;
    background: url('../../images/ourstory.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.storyText{
    width: 60%;
    padding-left: 3rem;
    padding-right: 2rem;
}

/* _________________________________________________________________________________________________________ */

@media(max-width:575px){
    .story{
        display: block;
    }
    .aboutImage{
        height: 60vh;
        width: 100%;
    }
    .storyText{
        width: 100%;
        padding-left: 0;
    }
}