:root{
  font-size: 62.5%;
}

*, ::after , ::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* __________________________________________________________ UTILITY CLASSES ___________________________________________________ */
.fontLarge{
  font-size: 2.9rem;
}

.fontMedium{
  font-size: 2.1rem;
}
.fontSmall{
  font-size: 1.7rem;
}

.largeMarginBottom{
  margin-bottom: 3rem;
}
.mediumMarginBottom{
  margin-bottom: 1.2rem;
}
.smallMarginBottom{
  margin-bottom: 0.6rem;
}

.lightFont{
  color: #676767;
}
.greenFont{
  color:mediumspringgreen;
}

.containerHeading{
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 3rem;
}

.btnCheckout{
  display: block;
  width: 90%;
  padding: 0.8rem 0.4rem;
  border-radius: 5px;
  border: 2px solid #676767;
  margin: 3rem auto;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.25s ease-in;
}

.btnCheckout:hover{
  transform: scale(1.01);
  color: mediumspringgreen;
  box-shadow: 0px 0px 0.5rem 0px black;
}
.btnCheckout:active{
  transform: scale(0.98);
  color: black;
  box-shadow: 0px 0px 0.5rem 0px black;
}

.btnSecondary{
  color: mediumspringgreen;
  border: 2px solid mediumspringgreen;
  width: 5rem;
  text-decoration: none;
  font-weight: bold;
  padding: 0.5rem 1.8rem;
  border-radius: 2rem;
  transition: all 0.4s ease-in-out;
}
.btnSecondary:hover{
  /* transform: scale(1.05); */
  background-color: mediumspringgreen;
  color: white;
  border:2px solid white;
  box-shadow: 0px 0px 60px 0 rgba(0,255,154,0.8);
}
.btnSecondary:active{
  font-size: 1.4rem;
}

a{
  color: white;
  text-decoration: none;
}

select{
  display: inline;
  width: 100%;
  cursor: pointer;
  border-radius: 5rem;
  padding: 0.25rem 0;
}

option{
  display: inline-block;
  text-align: center;
  background-color: #676767;
  color: white;
  font-weight: bold;

}
option:hover{
  background-color: white;
  color: mediumspringgreen;
}


/* ********************************************************* MEDIA QUERIES ********************************************************************************* */
@media(max-width:775px){
  .fontLarge{
    font-size: 2.6rem;
  }
  .fontMedium{
    font-size: 1.7rem;
  }
  .fontSmall{
    font-size: 1.25rem;
  } 

  .largeMarginBottom{
    margin-bottom: 2.4rem;
  }
  .mediumMarginBottom{
    margin-bottom: 1rem;
  }
  .smallMarginBottom{
    margin-bottom: 0.5rem;
  }
}

@media(max-width:325px){
  .fontLarge{
    font-size: 2rem;
  }
  .fontMedium{
    font-size: 1.5rem;
  }
  .fontSmall{
    font-size: 1.2rem;
  } 

  .largeMarginBottom{
    margin-bottom: 2rem;
  }
  .mediumMarginBottom{
    margin-bottom: 0.9rem;
  }
  .smallMarginBottom{
    margin-bottom: 0.45rem;
  }
}