#product{
    display: inline-block;
    height: 52vh;
    min-width: 21rem;
    max-width: 23rem;
    flex : 1 1 22rem;
    border-radius: 1rem;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.8);
    border: none;
    overflow: hidden;
    margin: 2rem 1rem;
}

#product img{
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
    transition: all 1s ease-in-out;
}
#product img:hover{
    transform: scale(1.1);
}

.productImg{
    object-position: center;
    height: 70%;
    max-height: 240px;
}


.productDetailsShort{
    height: 28.5rem;
    border: none;
    position: relative;
    padding: 0.6rem 1rem;
    padding-bottom: 0;
    top: -6%;
    transition: all 0.4s ease-in-out;
    background-color: white;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
}
.productDetailsShort:hover{
    top: -45%;
}

.productName{
    margin-top: 0.2rem;
    padding-bottom: 0.6rem;
}

.weight , .price{
    display: flex;
    justify-content: space-between;
    padding-bottom: 0.2rem;
}

.addToCartBtn, .productDetailsBtnLink{
    margin: 1rem auto;
    font-weight: bold;
    background-color: white;    
}
.productDetailsBtnLink{
    color: black;
}
.addToCartBtn:hover, .productDetailsBtnLink:hover{
    transform: scale(0.95);
    color: mediumspringgreen;
}
.addToCartBtn:active, .productDetailsBtnLink:active{
    color: midnightblue;
}

/* ___________________________________________________________ Media Queries ______________________________________________________________*/

@media(max-width:775px){
    #product{
        display: inline-block;
        height: 49vh;
        min-width: 19rem;
        max-width: 20rem;
        flex : 1 1 20rem;
    }

    .productDetailsShort{
        top: -2%;
    }

    .productDetailsShort:hover{
        top: -35%;
    }
}

@media(max-width:525px){
    #product{
        height: 100%;
        min-width: 19rem;
        max-width: 20rem;
    }
 
    .productDetailsShort{
        height: 100%;
        padding-bottom:1rem ;
    }
    .productDetailsShort:hover{
        top: 0%;
    }

}