.mainSubDetailsBox{
    font-weight: bold;
    text-align: center;
    margin-bottom: 0.5px;
    padding-bottom: 2rem;
    padding-top: 8rem;
    /* border-bottom: 2px solid mediumspringgreen; */
    background : url(../../images/contactBg.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#ProductDetails{
    padding: 4rem 2rem;
    padding-top: 5rem;
    display: flex;
    justify-content: space-evenly;
    gap: 1rem;
    /* background-color: rgba(255, 228, 196, 0.497); */
    
}
.productDetailsLeftArea{
    /* border: 1px solid black; */
    width: 30%;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.productDetailsLeftArea img{
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.5);
    border : 0.2px solid black;
    border-radius: 1rem;
    display: inline-block;
    width: 95%;
    height: 95%;
}

.productDetailsRightArea{
    width: 65%;
    padding: 0 1rem;
}
.productDetailsRightArea .CDLValue{
    text-align: left;
    width: 50%;
    display: inline-block;
}

.detailsAboutBox{
    background-color: mediumspringgreen;
    /* outline: 1px solid rgba(0,0,0,0.5); */
    text-align: start;
    padding: 0.4rem 1rem;
    color: white;
}
.margintop{
    margin-top: 5rem;
}


@media(max-width:775px){
    .productDetailsLeftArea{
        border: 1px solid black;
        width: 25rem;
        height: 40vh;
        display: block;
        margin: 1rem auto;
    }
    .productDetailsLeftArea img{
        display: inline-block;
        width: 100%;
        height: 100%;
    }

    .productDetailsRightArea{
        width: 67%;
        padding: 0rem;
        padding-left: 1rem;
    }
}

@media(max-width:475px){

    #ProductDetails{
        padding: 0rem 2rem;
        padding-top: 4rem;
        display: block;
    }

    .productDetailsLeftArea{
        border: 1px solid black;
        width: 60%;
        min-width: 22rem;
        height: 42vh;
        margin-bottom: 2.5rem;
    }

    .productDetailsRightArea{
        padding: 0rem;
        width: 100%;
    }
}