#checkout{
    padding-top: 8rem;
}

.checkout{
    display: flex;
    gap: 1rem;
    justify-content: space-evenly;
    padding: 4rem 0;
    padding-top: 6rem;
    background-color: lightcyan;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.6);
}

.checkoutMainHeading{
    text-align: center;
    margin: 1rem;
    margin-bottom: 2rem;
}

.checkoutSubHeadings{
    margin-bottom: 1.5rem;
}

/* ^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ Left Checkout Area ^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ */

.leftCheckoutArea{
    width: 60%;
    padding: 2rem 1.5rem;
    background-color: white;
}

.addressSelector{
    align-items: center;
    padding: 1rem 2rem;
}

.lastAddress, .lastAddress2{
    display: inline-block;
    width: 90%;
    margin: 0.1rem auto;
    margin-top: 1rem;
    padding: 0 2.5rem;
}
.lastAddress2{
    padding-left: 3.7rem;
}

.formBisector{
    color: #676767;
    text-align: center;
    margin: 2rem auto;
}

/* ____________________________________________________ */

.shipFormText{
display: block;
width: 90%;
margin: 0.1rem auto;
margin-top: 1.5rem;
}

.inputBoxCheckout, .stateInputBox{
    display: block;
    width: 90%;
    padding: 0.8rem;
    border-radius: 5px;
    border: 2px solid #676767;
    margin: 0.5rem auto;
    margin-bottom: 2.5rem;
}

/* ^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ Right Checlout Area ^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ */

.rightCheckoutArea{
    min-height: 100%;
    height: 100vh;
    width: 35%; 
    padding: 1rem;  
}

.checkoutDetails , .shippingAddressDetails{
    border:1px solid #676767;
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.6);
    padding: 2rem 1.5rem;
    margin-bottom: 3rem;
    background-color: white;
}

.rightCheckoutArea .checkoutDetails p,.rightSection .checkoutDetails h2{
    display: inline;
}

.checkoutDetailsLine{
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
    gap: 0.5rem;
}

.CDLValue{
    text-align: right;
}

.btnContainerCheckout{
    text-align: center;
}




/* ____________________________________________________________________________________________________________________________________________ */

@media(max-width:575px){
    .checkout{
        display: block;
        width: 100%;
    }

    .leftCheckoutArea{
        width: 100%;
    }

    .rightCheckoutArea{
        height: fit-content;
        width: 100%; 
    }
}