#Orders{
    padding-top:12rem ;
}

#Orders li{
    list-style: none;
}

.myOrderHeading{
    text-align: center;
    font-weight: bold;
}

.checkoutProduct{
    background-color: #fafafa;
    padding: 1rem;
    display: flex;
    justify-content: space-evenly;
    gap: 1rem;
}

.checkoutProductDetails{
    width: 72%;
    padding: 0.5rem 0;

}

.checkoutProduct img{
    display: inline-block;
    background-color: mediumturquoise;
    height: 100px;
    width: 12%;
}

.orderProducts{
    border: 1px solid black;
}

.Order{
    margin: 8rem 5%;
    margin-top: 0rem;
}

.prodDetailBtn{
    color: #676767;
    font-weight: bold;
    transition: all 0.2s ease-in;
}
.prodDetailBtn:hover{
    color: mediumspringgreen;
}