.mainBackground{
    /* background: black; */
    background: url(../../images/background.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 110vh;
    display: flex;
    margin-bottom: 5rem;
}

.mainImg{
    height: 100%;
    box-shadow: -20px 0px 15px 0px rgba(0,0,0,0.8);
    width: 35%;
    object-fit: cover;
    object-position: center;
    border: none;
    outline: none;
    position: relative;
    right:-32%;
    animation:mainImgAnimation 2s forwards 0.5s ease-in-out;
}
@keyframes mainImgAnimation {
    100%{
        right:0%;
    }
}

.mainText{
    height: 100%;
    width: 70%;
    color: #B2FEFA;
    color: white;
    position: relative;
    left: -55%;
    animation:mainTextAnimation 2s forwards 0.5s ease-in-out;
}
@keyframes mainTextAnimation {
    100%{
        left:0%;
    }   
}

.mainTextContainer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    padding: 5rem;
    margin-left: 1rem;
}

.colorFont{
    color: mediumspringgreen;
}

#MainFront .mainTextContainer h1 {
    margin-bottom: 5rem;
    font-size: 3.5rem;
}
#MainFront .mainTextContainer h2 ,#MainFront .mainTextContainer p{
    margin: 2rem 0;
}
.productsBtn{
    margin-top: 4rem;
}

/* *******************************************************************************    FEATURE SECTION     ************************************************************** */

.features{
    display: flex;
    flex-wrap: wrap;
    gap : 2rem;
    padding: 5rem 2.5rem;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.8);
}

.feature{
    border: 1px solid darkblue;
    flex: 1 1 35rem;
    padding: 3rem 2rem;
    text-align: center;
    min-height: 16rem;    
    border: none;
    transition: all 0.8s ease-in-out;
    border-radius: 1rem;
    border-bottom-right-radius: 2rem;
    border-top-left-radius: 2rem;

}

.feature:hover{
    color: mediumspringgreen;
    background-color: white;
    transform: scale(1.05);
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.8);
    border: 1px solid black;
    transform: skew(-2deg);
    
}

.featureHeading{
    margin-bottom: 1rem;
    margin-top: 1.5rem;
}

/* _____________________________________________________________________________  PRODUCTS  ___________________________________________________________________ */
.productHome{
    text-align: center;
}

.pHBox{
    display: flex;
    flex-wrap: wrap;

}

#productIntro{
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    background-color: rgb(255, 232, 220);
}

.productHome .productIntro h2{
    margin-bottom: 1.5rem;
    font-family: 'Lucida Sans' ,Cochin, 'Trebuchet MS', 'Times New Roman';
}

.productHome .productIntro p{
    padding: 1rem;
    font-family:  monospace ,'Gill Sans MT' ,'Times New Roman','Trebuchet MS';
}

.shopNowBtn{
    display: inline-block;
    font-family:'Trebuchet MS';
    width: 20rem;
    margin-top: 3.5rem;
    background: transparent;
}

.poojaEssentials{
    flex-basis: 1 1 40rem;
    min-width: 70%;
    height: 70vh;
    background: url('../../images/poojaessentials.jpg');
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    object-fit: cover;
    object-position: center;
}

/* --------------------------------------------------------------------------------- */
#showcase{
    background : url(../../images/contactBg.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: fit-content;
  padding: 4rem 0;
  padding-top: 1rem;
  margin-top: 5rem;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.8);
}

.line{
    display: flex;
    width: fit-content;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    gap: 0.5rem;
}

.moreProducts{
    color: black;
}

/* ___________________________________________________________   FAQ SECTION   __________________________________________________________________ */

#faq{
    /* border: 1px solid green; */
    padding: 5rem 4%;
    padding-top: 6rem;
    box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.8);
    background-color: rgb(178, 254, 250);
    margin-top: 6rem;
}


.summaryFAQ{
    color: #232323;
    /* border: 1px solid darkblue; */
    font-weight: bold;
    padding: 1rem 3%;
}

.detailsFAQ{
    /* border: 1px solid darkorange; */
    padding: 1rem 5%;
    color: #676767;
    border-top: 1px solid mediumspringgreen;
    border-bottom: 1px solid mediumspringgreen;
}

.contactFAQLink{
    color: mediumspringgreen;
    padding-left: 1rem ;
    font-weight: bold;
    transition: all 0.2s ease-in;
}

.contactFAQLink:hover{
    color: black;
}
.contactFAQLink:active{
    color: white;
}




/* _____________________________________________________________________________ MEDIA QUERIES ___________________________________________________________________ */
/* -------------------------------------------------------------------------------------------------------------------------------------------*/

@media(max-width:775px){
    #MainFront .mainTextContainer h1{
        margin-bottom: 4rem;
        font-size: 3rem;
    }
    .pHBox{
        display: block;
    }
    
    #productIntro{
        width: 100%;
        padding-top: 5rem;
        padding-bottom: 5rem;
    }
    
    .productHome .productIntro h2{
        margin-bottom: 1.2rem;
   }
  
    .shopNowBtn{
        width: 18rem;
        margin-top: 2rem;
    }
       
    .poojaEssentials{
        flex-basis: 1 1 40rem;
        min-width: 100%;
        height: 60vh;
        background-position: left;
    }
}

/* .................................... */
@media(max-width:455px){
    .mainBackground{
        flex-wrap: wrap;
        height: 1550px;
        height: 170vh;
    }
    .mainText , .mainImg{
        height: 50%;
        width: 100%;
        height: 100vh;
    }
    .mainText{
        height: 100vh;
    }
    .mainImg{
        height: 65vh;
    }
    .mainTextContainer{
        padding: 2.5rem;
        /* padding-left: 1rem; */
    }
    #MainFront .mainTextContainer h1 {
        margin-bottom: 3.4rem;
        font-size: 2.8rem;
    }

    
}




